:root {
  --primary: #00000093;
  --secondary: #ffa600;
  --tertiary: #1fa5da;
  --light: #fafafa;
  --dark: #272f32;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Gill Sans", sans-serif;
  font-weight: 350;
  line-height: 1.5;
  font-size: 16px;
  color: var(--dark);

  background-image: url("assets/BART.jpg");
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Gill Sans", sans-serif;
  font-weight: 350;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
  
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--dark);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: underline;
}

header {
  background: var(--primary);
  text-decoration: none;
}

header a {
  color: var(--light);
}

.passport-header {
  color: #ffffff;
  padding-top: 20px;
  padding-bottom: 20px;
}

.passport-message {
  color: var(--tertiary)
}

ul {
  list-style-type: none;
}

/* button {
  font-size: 1rem;
  text-decoration: none;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 2px;
  background-color: var(--secondary);
} */

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 100%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  padding: 5px;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.error-text {
  color: var(--error);
}

.column {
  float: left;
  width: 100%;
  margin: 25px 0px 0px;
  padding: 25px;
  background-color:#ddd;
  opacity: 0.75;
}

.ticket-logo {
  width: 10%;
}

.signup-toggle {
  color: var(--tertiary);
  background-color: Transparent;
  background-repeat:no-repeat;
  border: none;
  width: 293px
}

@media screen and (max-width: 768px) {
  .card {
    width: 100%;
  }

  .map {
    margin-right: -105%;
    padding-top: 30px;
  }

  .container-mobile {
    padding-top: 110%;
    padding-right: 15%;
  }

  select {
    color: #000000;
    background-color: #ffa600;
    border: 6px solid transparent;
    padding: 5px; 
    width: 294px;
    text-indent: 0;
    font-size: 25px;
    
  }
}

/*  proj 3/ transfercard route testing  */
.bartmap {
  display: block;
  border: solid;
  width: 75%;
  opacity: 0.8;
  margin: 0;
}

.container-map {
  position: relative;
  display: flex;
  padding-left: 5%;
  padding-top: 2%;
}

.map {
  padding-right: 0;
}
.result {
  color: black;
  margin: auto;
  padding-left: 5%;
}

.custom-select {
  position: relative;
  padding-bottom: 10%;
}

.stations {
  margin-top: 10% ;
  padding-top: 0;
}

select {
  color: #000000;
  background-color: #ffa600;
  border: 6px solid transparent;
  padding: 5px; 
  width: 320px;
  text-indent: 20px;
  font-size: 25px;
}

.bg {
  opacity: 0.8;
  width: 90%;
  float: left;
  padding: 25px;
  background-color:#ddd;
}

/* add to cart button */

.atc {
  display: block;
  font-family: inherit;
  font-size: 14px;
  font-weight: 200;
  color: #000;
  width: 150px;
  text-decoration: none;
  border: none;
  outline: none;
  cursor: pointer;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.08);
  cursor: pointer;
  transition: all 0.25s cubic-bezier(0.02, 0.01, 0.47, 1);
  margin-left: 65px;
  padding: 5px;
  background-color: var(--secondary);
}
